/**
* Media queries
**/
.featured-section {
  padding: 150px 0;
  background-color: var(--clr-grey);
}
@media only screen and (min-width: 1440px) {
  .featured-section {
    padding-top: 200px;
  }
}
@media only screen and (min-width: 1440px) {
  .featured-section {
    padding-top: 436px;
  }
}
.featured-section .textbox {
  text-align: center;
}
@media only screen and (min-width: 992px) {
  .featured-section .textbox {
    text-align: left;
    margin-top: 80px;
  }
}
.featured-section h1 {
  max-width: 500px;
  margin: 0 auto 20px;
}
@media only screen and (min-width: 992px) {
  .featured-section h1 {
    margin: 0 0 20px;
  }
}
@media only screen and (min-width: 1440px) {
  .featured-section h1 {
    max-width: none;
  }
}
.featured-section p {
  margin-bottom: 20px;
}
@media only screen and (min-width: 992px) {
  .featured-section p {
    max-width: 461px;
    margin-bottom: 65px;
  }
}
.featured-section a.button {
  margin: 0 auto 50px;
}
@media only screen and (min-width: 992px) {
  .featured-section a.button {
    margin-top: 64px;
    margin-left: 0;
  }
}
.featured-section__slider-wrapper {
  position: relative;
}
.featured-section__circle-wrapper {
  display: flex;
  width: 100%;
  position: absolute;
  left: 0;
  transform-origin: center;
  margin: 0 auto;
  top: 30%;
}
@media only screen and (min-width: 992px) {
  .featured-section__circle-wrapper {
    top: 10%;
  }
}
.featured-section__circle {
  width: 50%;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  transform-origin: center;
}
.featured-section__circle--small {
  transform: scale(0.5);
}
.featured-section__slider {
  margin-top: 40px;
}
@media only screen and (min-width: 992px) {
  .featured-section__slider {
    margin-top: 0;
  }
}
.featured-section__slider .slide {
  position: relative;
}
.featured-section__slider .slide img {
  width: 100%;
  height: auto;
  max-width: 550px;
  margin: 0 auto;
}
@media only screen and (min-width: 992px) {
  .featured-section__slider .slide img {
    margin: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .featured-section__slider .slide img {
    max-width: none;
  }
}